import React from "react";
import { cashoutNodeTypeReward, payNodeTypeFee } from "../utils/contract";
import { limitNumOfNodes } from "constants/config";
import { calDateFromSeconds, getTimestamp, checkPayable } from "utils/util";

export default function AnalyticBoard(props) {

    // claim per node type
    const claimNodeType = async (address, nodeType) => {
        if (calNodeTypeReward(nodeType) >0 ) {
            let tx = await cashoutNodeTypeReward(address, nodeType.typeNum);
            console.log(tx);
        } else {
            alert("You don't have nothing to claim");
        }
    }

    // Pay fees of nodes belong to node type
    const payNodeType = async (address, nodeType) => {
        let _etherAmount = 0;
        
        props.nodes.forEach((node) => {
            if (checkPayable(node) && node.worrier == nodeType.title) _etherAmount+=node.fee;
        });
        if (_etherAmount > 0) {
            let tx = await payNodeTypeFee(address, nodeType.typeNum, _etherAmount);
            console.log(tx); 
        } else {
            alert("You don't have nothing to pay");
        }
    }

    // add up node rewards per node type
    const calNodeTypeReward = (nodeType) => {
        let reward = 0;
        props.nodes.forEach((node) => {
            let leftTime = calDateFromSeconds(node.paidTime - getTimestamp());
            if (node.worrier === nodeType.title && node.isAvailable=="1" && leftTime !== "-")
                reward += node.reward;
        });
        return reward.toFixed(5);
    }

    return (
        <>
            <div className="card">
                <div className="card-body">
                    <div className="row">
                        {/* My node ccount and total number of created nodes */}
                        <div className="col-6">
                            <div>My Nodes: {props.nodesLength}/{limitNumOfNodes}</div>
                            <div>Total Nodes: {props.totalNodes}</div>
                            <div>
                                {props.nodeTypes.map((nodeType, i) => (
                                    <div key={i}>
                                        {nodeType.title} : {nodeType.totalRewardReleased}
                                    </div>
                                ))}
                            </div>
                        </div>
                        {/* Amount of added rewards per node type */}
                        <div className="col-6">
                            <h2>Rewards</h2>
                            <div>
                                {props.nodeTypes.map((nodeType, i) => (
                                    <div key={i}>
                                        {nodeType.title} : {calNodeTypeReward(nodeType)}---
                                        <button onClick={() => claimNodeType(props.address, nodeType)} type="button" className="btn btn-sm btn-dark">Claim</button>
                                        <button onClick={() => payNodeType(props.address, nodeType)} type="button" className="btn btn-sm btn-dark">Pay Fee</button>
                                    </div>
                                ))}
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </>
    );
}
