// import Web3 from 'web3';
// import { rpcUrl } from "../constants/config";
import { payDeadline } from "constants/config";

// estimate current block time stamp from browser time 
// it doesn't match exactly so it can cause reward amount is less than 0
export const getTimestamp = () => {
    // var web3 = new Web3(new Web3.providers.HttpProvider(rpcUrl));
    // var blockNumber =await web3.eth.getBlockNumber();
    // var block = await web3.eth.getBlock(blockNumber);
    let browserTimestamp = Math.floor(Date.now() / 1000);
    // console.log(block["timestamp"] - browserTimestamp, "difference");
    return browserTimestamp + 130;
}
export const calDateFromSeconds = (duration) => {
    duration = parseInt(duration);
    if (duration <= 0) return "-";
    else if (duration < 3600) {
        return (Math.floor(duration/60) + "mins " + duration%60 + "seconds");
    } else if (duration < 86400) {
        return (Math.floor(duration/3600) + "hours " + Math.floor((duration%3600)/60) + "mins");
    } else {
        return (Math.floor(duration/86400) + "days " + Math.floor((duration%86400)/3600) + "hours");
    }
}

export const checkPayable = (node) => {
    let leftTime = calDateFromSeconds(node.paidTime - getTimestamp());
    if (leftTime!="-"&&parseInt(leftTime)<2*payDeadline) return true;
    return false;
}