import { useState, useEffect } from "react";
import NodeComponent from "../components/NodeComponent";
import AnalyticBoard from "../components/AnalyticBoard";
import Input from "../components/Input";
import { nodeTypes, payDeadline } from "../constants/config";
import { 
    getBalance,
    createNodeWithTokens, 
    getNodesInfo, 
    getNodeType, 
    getTotalNodeNum,
    cashoutAll,
    cashoutNodeReward,
    payNodeFee,
    payAll
} from "../utils/contract";
import { getTimestamp, calDateFromSeconds, checkPayable } from "../utils/util";
import { connectState, connectedAddress } from "app/reducers/walletSlice";
import { useSelector } from 'react-redux';

export default function MainPage() {
    const [nodeType, setNodeType] = useState({});     // selected Node type
    const [nodeName, setNodeName] = useState("");     // Name of creating node
    const [nodesArray, setNodesArray] = useState([]); // nodes info owned by account
    const [totalNum, setTotalNum] = useState(0);      // total number of created nodes
    const [nodesNum, setNodesNum] = useState(0);      // number of nodes owned by account

    const connected = useSelector(connectState);
    const walletAddress = useSelector(connectedAddress);

    const createNode = async () => {
        let balance = await getBalance(walletAddress);
        // check account has enough token balance to create node
        if (balance >  nodeType.nodePrice) {
            let tx = await createNodeWithTokens(walletAddress, nodeType.typeNum, nodeName);
            console.log(tx);
        } else {
            alert("You don't have enough balance");
        }
    }

    // get number of all nodes
    const getTotalNumOfNodes = async () => {
        let totalNum = await getTotalNodeNum();
        setTotalNum(totalNum);
    }

    // get node types info from smart contract(node type number, reward per day, total released)
    const loadNodeTypesInfo = async () => {
        for(let i=0; i < nodeTypes.length; i++) {
            let _nodeType = await getNodeType(nodeTypes[i].typeNum);
            nodeTypes[i].nodePrice = _nodeType.nodePrice;
            nodeTypes[i].rewardPerDay = _nodeType.rewardPerDay;
            nodeTypes[i].totalRewardReleased = _nodeType.totalRewardReleased / Math.pow(10, 18);
            nodeTypes[i].fee = parseInt(_nodeType.fee);
        }
    }
    
    // get nodes information owned by account from smart contract
    // the return value of smart contract is one string so split string using #
    const loadNodesInfo = async () => {
         let nodesInfoArray = [];
        try {
            nodesInfoArray = await getNodesInfo(walletAddress);
        } catch (error) {
            console.log(error);
        }
        
        let nodes = [];
        for (let i=0; i<Math.floor(nodesInfoArray.length/6); i++) {
            nodes.push({
                name: nodesInfoArray[6*i+1],
                typeNum: nodesInfoArray[6*i+2],
                creationTime: nodesInfoArray[6*i+3],
                lastClaimTime: nodesInfoArray[6*i+4],
                paidTime: nodesInfoArray[6*i+5],
                isAvailable: nodesInfoArray[6*i+6],
            });
        }
        processNodesInfo(nodes);
    }

    // create nodes for showing in table
    const processNodesInfo = (_nodes) => {
        let nodes = [];
        let timestamp = getTimestamp();
        for(let i=0; i < _nodes.length; i++){
            let typeNum = parseInt(_nodes[i].typeNum) - 1;
            nodes.push({
                name: _nodes[i].name,
                worrier: nodeTypes[typeNum].title,
                reward: (timestamp - _nodes[i].lastClaimTime) * (parseInt(nodeTypes[typeNum].rewardPerDay) / 86400) / Math.pow(10, 18),
                creationTime: _nodes[i].creationTime,
                paidTime: _nodes[i].paidTime,
                isAvailable: _nodes[i].isAvailable,
                fee: nodeTypes[typeNum].fee,
            });
        }
        setNodesNum(nodes.length);
        setNodesArray(nodes);
    }
    
    // Refresh page with updated nodes info, node types, total node num
    const refresh = async () => {
        await getTotalNumOfNodes();
        await loadNodeTypesInfo();
        await loadNodesInfo();
    }

    const initilize = async () => {
        await getTotalNumOfNodes();
        await loadNodeTypesInfo();
        await loadNodesInfo();
    }

    // claim reward of node created at specific time
    const claimNode = async (node) => {
        if (checkPayable(node)) {
            let tx = await cashoutNodeReward(walletAddress, node.creationTime);
            console.log(tx);
        } else {
            alert("You can't claim this node");
        }
    }
    // pay fee of node created at specific time
    const payNode = async (node) => {
        if (checkPayable(node)) {
            let tx = await payNodeFee(walletAddress, node.creationTime, node.fee);
        } else {
            alert("You can't pay");
        }
        console.log(tx);
    }

    // pay fees of all nodes owned by account
    const payAllNodes = async () => {
        let _etherAmount = 0;
        
        nodesArray.forEach((node) => {
            if (checkPayable(node)) _etherAmount+=node.fee;
        });
        if (_etherAmount > 0) {
            let tx = await payAll(walletAddress, _etherAmount);
            console.log(tx); 
        } else {
            alert("You don't have nothing to pay");
        }
    }
    // claim rewards of all nodes owned by account
    const claimAllNodes = async () => {
        let _etherAmount = 0;        
        nodesArray.forEach((node) => {
            if (checkPayable(node)) _etherAmount+=node.fee;
        });
        if (_etherAmount > 0) {
            let tx = await cashoutAll(walletAddress);
            console.log(tx);
        } else {
            alert("You don't have nothing to claim");
        }
    }

    // Reload page when connected, address of store are changed
    useEffect(async () => {
        if (connected) {
            await initilize();
            const interval = setInterval(async () => {
                await initilize();
            }, 5000);
            return () => clearInterval(interval);
        } else {
            setNodesNum(0);
            setNodesArray([]);
        }
    }, [connected, walletAddress]);
    
    return (
        <>
            <div>
                {/* Refresh button */}
                <div>
                    <button onClick={refresh} type="button" className="btn btn-dark">
                        Refresh
                    </button>
                </div>
                {/* Board for total node num, total released and claimable rewards per node type */}
                <AnalyticBoard 
                    nodesLength={nodesNum}
                    nodeTypes={nodeTypes}
                    totalNodes={totalNum}
                    nodes={nodesArray}
                    address={walletAddress}
                />
                {/* Showing Node types */}
                <div className="d-flex justify-content-between" >
                    {nodeTypes.map((nodeType, i) => (
                        <div key={i} onClick={() => setNodeType(nodeType)}>
                            <NodeComponent 
                                key={i}
                                title={nodeType.title} 
                                path={nodeType.imgPath} 
                            />
                        </div>
                    ))}
                </div>
                {/* Creating new node */}
                <div className="mt-4">
                    <Input
                        placeholder={"Name your Node"}
                        onChange={e => setNodeName(e.target.value)}
                    />
                    <div className="mt-3">
                        <button type="button" className="btn btn-secondary" onClick={createNode}>Create Node</button>
                    </div>
                </div>
                {/* Claim all button */}
                <button onClick={() => claimAllNodes()} type="button" className="mt-4 btn btn-dark">
                    Claim All
                </button>
                <button onClick={() => payAllNodes()} type="button" className="mt-4 btn btn-dark">
                    Pay All
                </button>
                {/* Table for showing all nodes owned by account */}
                <table className="mt-4 table table-dark table-hover">
                    <thead>
                    <tr>
                        <th>My Nodes</th>
                        <th>Worrior</th>
                        <th>RPC</th>
                        <th>Rewards</th>
                        <th>Deadline To Pay Fee</th>
                        <td>Pay</td>
                        <td>Cash Out</td>
                    </tr>
                    </thead>
                    <tbody>
                        {nodesArray.map((node, i) => (
                            <tr key={i}>
                                <td>{node.name}</td>
                                <td>{node.worrier}</td>
                                <td>-</td>
                                <td>{Math.max(node.reward, 0).toFixed(10)}</td>
                                <td>{node.isAvailable=="1"? calDateFromSeconds(node.paidTime - getTimestamp()) : "Not Available" }</td>
                                {/* <td>{node.isAvailable=="1"? node.paidTime : "Not Available" }</td> */}
                                <td>            
                                    <button onClick={() => payNode(node)} type="button" className="btn btn-dark">
                                        Pay
                                    </button>
                                </td>
                                <td>            
                                    <button onClick={() => claimNode(node)} type="button" className="btn btn-dark">
                                        Claim
                                    </button>
                                </td>
                            </tr>
                        ))}
                    </tbody>
                </table>
            </div>
        </>
    );
}
